// main: ../style.scss
@import '../global/vars';
@import '../global/mixin'; // header

#preheader {
  //position: relative;
  //display: block;
  //width: 100%;
}

#header {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  display: block;
  width: 100vw;
  -webkit-transition: all 0.2s linear, width 0s;
  -moz-transition: all 0.2s linear, width 0s;
  -o-transition: all 0.2s linear, width 0s;
  transition: all 0.2s linear, width 0s;

  &.hidden {
    pointer-events: none;
  }
}

#header-structure {
  position: relative;
  @include flex-wrap;
  @include flex-verticalize;

  .logo {
    position: relative;
    -webkit-box-flex: 1;

    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 100%;
      background-image: url("assets/images/site-logo-header.svg");
      background-position: 0 50%;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  .menu {
    -webkit-box-flex: 1;
  }

  .side-head {
    -webkit-box-flex: 1;
  }
}

#head-overlay {
  position: fixed;
  z-index: 9997;
  left: 0;
  display: block;
  width: 100vw;
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  @include define-basic-transition(all, 0.3s);

  &.hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }
}

.hide-y-overflow {
  overflow-y: hidden;
}

.scroll-opportunity {
  position: relative;
  display: block;
  overflow-y: auto;

  .wrapper {
    overflow: hidden;
  }
}

// footer

#footer {
  position: relative;
  display: block;
  width: 100%;
  overflow: hidden;
}